import React, { useEffect } from "react";
import Navigation from "@/components/Navigation";
import { Redirect, Route } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { actions, selectors } from "@/data";

const DefaultLayout = ({ component: Component, logOut, ...rest }) => {
  const user = useSelector(selectors.users.getUser);
  const whoAmIRemote = useSelector(selectors.users.getWhoAmI);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.user.whoAmI());
  }, []);

  return whoAmIRemote.cata({
    NotAsked: () => "Loading",
    Loading: () => "Loading",
    Failure: () => <Redirect from="" to="/login" />,
    Success: () => (
      <Route
        {...rest}
        render={matchProps => (
          <>
            <Navigation user={user} onLogout={logOut} />
            <Component {...matchProps} {...rest} />
            <style jsx global>{`
              * {
                box-sizing: border-box;
              }

              html,
              body {
                font-family: Dotum, "맑은 고딕", "roboto", "Helvetica Neue",
                  Helvetica, Arial, "맑은 고딕", malgun gothic, "돋움", Dotum,
                  sans-serif;
                color: #202b3d;
                background-color: #e9eaed;
                font-size: 12px;
                font-weight: 400;
                line-height: 1.5;
              }

              body {
                padding: 100px 0;
              }
            `}</style>
          </>
        )}
      />
    )
  });
};

export default DefaultLayout;
