import React from "react";
import {useDispatch, useSelector} from "react-redux";
import { Field, Form, Formik } from 'formik';
import {actions, actionTypes as AT} from '@/data'
import TextAreaInput from "@/components/Form/TextAreaInput";
import { composeValidators, required, validPost } from "@/services/FormHelper/validators";
import Button from "@/components/Button";
import {createLoadingSelector} from "@/data/utils";

const INITIAL_VALUES = { contents: '' };
const selectIsSubmitting = createLoadingSelector([AT.ADD_POST]);

const PostForm = props => {
  const {
    minHeight = 100,
    lineHeight = 20,
    placeholder = "무슨 생각을 하고 계신가요?"
  } = props;
  const dispatch = useDispatch();

  const handleSubmit = ({ contents }, { resetForm }) => dispatch(actions.posts.addPost({ contents, resetForm }));
  const isSubmitting = useSelector(selectIsSubmitting);

  return (
    <>
      <Formik initialValues={INITIAL_VALUES} onSubmit={handleSubmit}>
        {
          ({ errors }) =>
            <Form className="write-form">
              <Field
                lineHeight={lineHeight}
                validate={composeValidators(required, validPost)}
                component={TextAreaInput}
                name="contents"
                className="form-control input-lg"
                placeholder={placeholder}
                spellCheck="false"
              />
              <Button
                isBusy={isSubmitting}
                type="submit"
                className="btn btn-primary"
                disabled={Object.values(errors).length > 0}
              >
                공유하기
              </Button>
            </Form>
        }
      </Formik>
      <style jsx global>{`
        .write-form textarea.form-control {
          min-height: ${minHeight}px;
          line-height: ${lineHeight}px;
          padding: 20px;
          font-size: 18px;
          resize: none;
          border: none;
          border-radius: 0.5rem;
          transition: box-shadow ease-in-out 1s;
        }
        .write-form > textarea:focus {
          box-shadow: #999999 0 0 50px;
        }
        .write-form > button.btn {
          float: right;
          margin-bottom: 0;
          margin-top: 16px;
          background-color: #3b5999;
          color: #fffffe;
          border: none;
          font-weight: 800;
        }
        .write-form {
          margin-bottom: 100px;
        }
      `}</style>
    </>
  );
};

export default PostForm;
