import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { actions, actionTypes as AT } from '@/data';
import { Field, Form, Formik } from 'formik';
import TextAreaInput from '@/components/Form/TextAreaInput';
import { composeValidators, required, validComment } from '@/services/FormHelper/validators';
import Button from '@/components/Button';
import { createLoadingSelector } from '@/data/utils';

const INITIAL_VALUES = { contents: '' };
const selectIsSubmitting = createLoadingSelector([AT.CREATE_COMMENT]);

const CommentForm = (props) => {
  const dispatch = useDispatch();
  const { postId, minHeight = 20, lineHeight = 20, placeholder = '댓글을 입력하세요...' } = props;

  const handleSubmit = ({ contents }, { resetForm }) => {
    dispatch(actions.comments.createComment({ postId, contents, resetForm }));
  };

  const isSubmitting = useSelector(selectIsSubmitting);

  return (
    <>
      <Formik initialValues={INITIAL_VALUES} onSubmit={handleSubmit}>
        {({ errors }) => (
          <Form className="comment-form">
            <Field
              lineHeight={lineHeight}
              validate={composeValidators(required, validComment)}
              component={TextAreaInput}
              name="contents"
              className="form-control input-lg"
              placeholder={placeholder}
              spellCheck="false"
            />
            <Button
              isBusy={isSubmitting}
              type="submit"
              className="btn btn-primary"
              disabled={Object.values(errors).length > 0}>
              댓글달기
            </Button>
          </Form>
        )}
      </Formik>
      <style jsx global>{`
        .comment-form {
          margin: 20px;
        }
        .comment-form > textarea.form-control {
          min-height: ${minHeight}px;
          line-height: ${lineHeight}px;
          border-radius: 0.5rem;
          resize: none;
        }
        .comment-form > button.btn {
          float: right;
          margin-bottom: 0;
          margin-top: 16px;
          background-color: #3b5999;
          color: #fffffe;
          border-color: unset;
          font-weight: 800;
        }
      `}</style>
    </>
  );
};

export default CommentForm;
