import apis from '@/apis';
import { actions } from '@/data';
import { select, call, put } from 'redux-saga/effects';
import { mergeListBySeq } from '@/data/utils';
import { getLocation } from 'connected-react-router';
import * as selectors from '@/data/rootSelectors';
import { isNil, isEmpty } from 'ramda';

export function* addPost({ contents, resetForm }) {
  try {
    const offset = yield select(selectors.posts.getOffset);
    yield put(actions.posts.addPostLoading());
    yield call(apis.postsApi.createPost, contents);
    resetForm();

    yield put(actions.posts.getPosts(0, 1));
    yield put(actions.posts.addPostSuccess());
  } catch (error) {
    yield put(actions.posts.addPostFailure());
    alert(error.message);
  }
}

export function* getPosts({ offset, limit, refresh }) {
  const location = yield select(getLocation);
  const isUserPath = location?.pathname?.match(/\/u\/.*/);
  const userId = location?.pathname?.replace(/\/u\//, '');
  console.log(isUserPath);
  console.log(userId);
  console.log(refresh);
  try {
    const user = yield select(selectors.users.getUser);
    const prev = yield select(selectors.posts.getPosts);
    const posts = yield apis.postsApi.getAllPosts({
      userId: isNil(isUserPath) || isEmpty(userId) ? user.id : userId,
      offset: refresh ? 0 : offset,
      limit,
    });
    console.log(posts);
    const allPosts = refresh ? posts : mergeListBySeq(prev, posts);
    yield put(actions.posts.getPostsSuccess(allPosts));
    yield put(actions.posts.setOffset(allPosts.length));
  } catch (error) {
    console.log('error', error);
    yield put(actions.posts.getPostsFailure(error));
    alert(error.message);
  }
}

export function* likePost({ postId }) {
  try {
    yield put(actions.posts.likePostLoading());

    const user = yield select(selectors.users.getUser);
    const updatedPost = yield apis.postsApi.likePost({ userId: user.id, postId });
    yield put(actions.posts.likePostSuccess(updatedPost));

    // yield put(actions.posts.getPosts());
  } catch (error) {
    yield put(actions.posts.likePostFailure());
    alert(error.message);
  }
}
