import * as AT from './actionTypes';
import Remote from '@/data/remote';
import { curry, map, when, propEq, assoc } from 'ramda';

const alter = curry((checked, key, items) => map(when(propEq('key', key), assoc('good', checked)), items));

const INITIAL_STATE = {
  [AT.GET_POSTS]: Remote.NotAsked,
  [AT.ADD_POST]: Remote.NotAsked,
  [AT.LIKE_POST]: Remote.NotAsked,
  [AT.SET_POST_OFFSET]: 0,
};

function posts(state = INITIAL_STATE, action = {}) {
  switch (action.type) {
    // Get Posts
    case AT.GET_POSTS:
      return { ...state, offset: action.offset };
    case AT.GET_POSTS_SUCCESS:
      return { ...state, [AT.GET_POSTS]: Remote.Success(action.data) };
    case AT.GET_POSTS_FAILURE:
      return { ...state, [AT.GET_POSTS]: Remote.Failure(action.error) };
    // Add Post
    case AT.ADD_POST_LOADING:
      return { ...state, [AT.ADD_POST]: Remote.Loading };
    case AT.ADD_POST_SUCCESS:
      return { ...state, [AT.ADD_POST]: Remote.Success(action.data) };
    case AT.ADD_POST_FAILURE:
      return { ...state, [AT.ADD_POST]: Remote.Failure(action.error) };
    // Like Post
    case AT.LIKE_POST_LOADING:
      return { ...state, [AT.LIKE_POST]: Remote.Loading };
    case AT.LIKE_POST_SUCCESS:
      return {
        ...state,
        [AT.LIKE_POST]: Remote.Success(action.data),
        [AT.GET_POSTS]: state[AT.GET_POSTS].map((v) => {
          return v.map((post) => (post.id == action.data.id ? action.data : post));
        }),
      };
    case AT.LIKE_POST_FAILURE:
      return { ...state, [AT.LIKE_POST]: Remote.Failure(action.error) };
    case AT.SET_POST_OFFSET:
      return { ...state, [AT.SET_POST_OFFSET]: action.offset };
    default:
      return state;
  }
}

export default posts;
