import * as AT from './actionTypes';

export const addPost = ({ contents, resetForm }) => ({
  type: AT.ADD_POST,
  contents,
  resetForm,
});

export const addPostLoading = () => ({
  type: AT.ADD_POST_LOADING,
});

export const addPostSuccess = (data) => ({
  type: AT.ADD_POST_SUCCESS,
  data,
});

export const addPostFailure = (error) => ({
  type: AT.ADD_POST_FAILURE,
  error,
});

export const getPosts = (offset, limit = 5, refresh = false) => ({
  type: AT.GET_POSTS,
  offset,
  limit,
  refresh,
});

export const getPostsSuccess = (data) => ({
  type: AT.GET_POSTS_SUCCESS,
  data,
});

export const getPostsFailure = (error) => ({
  type: AT.GET_POSTS_FAILURE,
  error,
});

export const likePost = (postId) => ({
  type: AT.LIKE_POST,
  postId,
});

export const likePostLoading = () => ({
  type: AT.LIKE_POST_LOADING,
});

export const likePostSuccess = (data) => ({
  type: AT.LIKE_POST_SUCCESS,
  data,
});

export const likePostFailure = (error) => ({
  type: AT.LIKE_POST_FAILURE,
  error,
});

export const setOffset = (offset) => ({
  type: AT.SET_POST_OFFSET,
  offset,
});
