import * as AT from './actionTypes';
import Remote from "@/data/remote";

const initialState = {
  [AT.CREATE_COMMENT]: Remote.NotAsked,
  [AT.GET_COMMENTS]: Remote.NotAsked
};

export default function comments(state = initialState, action = {}) {
  switch (action.type) {
    case AT.SET_COMMENTS: {
      return {
        ...state,
        [action.postId]: action.comments,
      };
    }
    case AT.CREATE_COMMENT_LOADING: {
      return {
        ...state,
        [AT.CREATE_COMMENT]: Remote.Loading,
      };
    }
    case AT.CREATE_COMMENT_SUCCESS: {
      return {
        ...state,
        [AT.CREATE_COMMENT]: Remote.Success(action.data),
      };
    }
    case AT.CREATE_COMMENT_FAILURE: {
      return {
        ...state,
        [AT.CREATE_COMMENT]: Remote.Failure(action.error),
      };
    }
    case AT.GET_COMMENTS_SUCCESS: {
      return {
        ...state,
        [AT.GET_COMMENTS]: Remote.Success(action.data),
      };
    }
    case AT.GET_COMMENTS_FAILURE: {
      return {
        ...state,
        [AT.GET_COMMENTS]: Remote.Failure(action.error),
      };
    }
    default:
      return state;
  }
}
