import * as AT from './actionTypes';

export const setComments = (postId, comments) => ({
  type: AT.SET_COMMENTS,
  postId,
  comments,
});

export const getComments = (postId) => ({
  type: AT.GET_COMMENTS,
  postId,
});

export const getCommentsSuccess = (data) => ({
  type: AT.GET_COMMENTS_SUCCESS,
  data,
});

export const getCommentsFailure = (error) => ({
  type: AT.GET_COMMENTS_FAILURE,
  error,
});

export const createCommentLoading = () => ({
  type: AT.CREATE_COMMENT_LOADING,
});

export const createCommentSuccess = (data) => ({
  type: AT.CREATE_COMMENT_SUCCESS,
  data,
});

export const createCommentFailure = (error) => ({
  type: AT.CREATE_COMMENT_FAILURE,
  error,
});

export const createComment = ({ postId, contents, resetForm }) => ({
  type: AT.CREATE_COMMENT,
  postId,
  contents,
  resetForm,
});
