import React from 'react';
import toggle from '/hocs/toggle';
import * as actions from '@/data/rootActions';
import { useDispatch } from 'react-redux';

const Profile = (props) => {
  const { profileImageUrl, name, id } = props.user;
  const dispatch = useDispatch();
  const handleClickUser = (event) => {
    event.preventDefault();
    dispatch(actions.router.push(`/u/${id}`));
  };

  return (
    <li className="nav-item">
      <a className="nav-link" onClick={handleClickUser}>
        {profileImageUrl ? <img src={profileImageUrl} alt="" /> : false} {name}
      </a>
      <style jsx>{`
        .nav-item img {
          width: 25px;
          height: 25px;
          border-radius: 100%;
          overflow: hidden;
          margin-right: 5px;
        }
      `}</style>
    </li>
  );
};

export default toggle(Profile);
