import { getAuthCookie } from '@/data/cookie';

export const socialApiRequestInterceptor = (request) => {
  const token = getAuthCookie();
  if (token) {
    request.headers['Authorization'] = token;
  }
  return request;
};

export const socialApiResponseInterceptor = [
  (res) => {
    const { response, error } = res.data;
    if (error) {
      return Promise.reject(JSON.stringify(error, null, 2));
    } else {
      return response;
    }
  },
  (error) => Promise.reject(error),
];
