import React, { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import * as actions from '@/data/rootActions';
import * as selectors from '@/data/rootSelectors';
import useIntersect from '@/hooks/useIntersect';
import PostLoading from '@/components/PostLoading';
import Post from './Post';
import PostForm from './PostForm';

const byCreateAt = (left, right) => +new Date(right.createAt) - +new Date(left.createAt);

const Home = () => {
  const dispatch = useDispatch();
  const [node, entry] = useIntersect({ threshold: 1 });
  const posts = useSelector(selectors.posts.getPosts);
  const offset = useSelector(selectors.posts.getOffset);
  const postList = useMemo(() => posts.sort(byCreateAt).map((post) => <Post key={post.id} post={post} />), [posts]);
  useEffect(() => {
    if (entry.isIntersecting && posts.length === offset) {
      dispatch(actions.posts.getPosts(offset));
    }
  }, [entry.isIntersecting]);
  return (
    <div className="posts container">
      <PostForm />
      {postList}
      <PostLoading ref={node} isEnd={posts.length == offset} />
      <style jsx>{`
        .container {
          max-width: 600px;
        }
      `}</style>
    </div>
  );
};

export default Home;
