import axios from "axios";
import {
  socialApiRequestInterceptor,
  socialApiResponseInterceptor
} from "./helpers";

const SOCIAL_SERVER_URL = "http://localhost:9000/api/v1";

const socialApiClient = axios.create({
  baseURL: SOCIAL_SERVER_URL
});

socialApiClient.interceptors.request.use(socialApiRequestInterceptor);
socialApiClient.interceptors.response.use(...socialApiResponseInterceptor);

export { socialApiClient };
