import React, { useMemo, useEffect } from 'react';
import Post from '../Home/Post';
import { useDispatch, useSelector } from 'react-redux';
import * as selectors from '@/data/rootSelectors';
import * as actions from '@/data/rootActions';
import { useParams } from 'react-router-dom';

const byCreateAt = (left, right) => +new Date(right.createAt) - +new Date(left.createAt);

const User = () => {
  const dispatch = useDispatch();
  const posts = useSelector(selectors.posts.getPosts);
  const { userId } = useParams();
  const offset = useSelector(selectors.posts.getOffset);
  const postList = useMemo(() => posts.sort(byCreateAt).map((post) => <Post key={post.id} post={post} />), [posts]);

  useEffect(() => {
    dispatch(actions.posts.getPosts(offset, 5, true));
  }, [userId]);

  return (
    <div className="container">
      {postList}
      <style jsx>{`
        .container {
          max-width: 600px;
        }
      `}</style>
    </div>
  );
};

export default User;
