import { call, put } from 'redux-saga/effects';
import { actions } from '@/data';
import { clearAuthCookie, getAuthCookie, setAuthCookie } from '@/data/cookie';
import apis from '@/apis';

export function* login(action) {
  try {
    yield put(actions.user.loginLoading());

    const { email, password } = action;
    const userSession = yield apis.authApi.login({ principal: email, credentials: password });
    yield put(actions.user.setUserSession(userSession));
    setAuthCookie(userSession.apiToken);
    yield put(actions.user.loginSuccess(userSession));

    yield put(actions.router.push('/'));
  } catch (e) {
    yield put(actions.user.loginFailure(e));
    alert(e);
  }
}

export function* logout() {
  yield put(actions.user.resetAuth());
  clearAuthCookie();
  yield put(actions.router.push('/login'));
}

export function* register(action) {
  try {
    yield put(actions.user.registerLoading());

    const { formValues } = action;
    const userSession = yield apis.usersApi.register(formValues);
    yield put(actions.user.setUserSession(userSession));
    setAuthCookie(userSession.apiToken);
    yield put(actions.router.push('/'));

    yield put(actions.user.registerSuccess(userSession));
  } catch (e) {
    yield put(actions.user.registerFailure(e));
    alert(e);
  }
}

export function* whoAmI() {
  try {
    yield put(actions.user.whoAmILoading());

    const maybeValidToken = getAuthCookie();
    const user = yield call(apis.usersApi.whoAmI, maybeValidToken);
    yield put(actions.user.setUserSession({ apiToken: maybeValidToken, user }));
    yield put(actions.user.whoAmISuccess(user));
  } catch (e) {
    yield put(actions.user.whoAmIFailure(e));
    yield put(actions.user.resetAuth());
    yield put(actions.router.push('/login'));
  }
}
