import { socialApiClient } from '@/apis/clients';

export const api = {
  async createPost(contents) {
    try {
      await socialApiClient.post('/posts', { contents });
    } catch (e) {
      throw Error(e.message);
    }
  },

  async createComment({ userId, postId, contents }) {
    try {
      return await socialApiClient.post(`/users/${userId}/posts/${postId}/comments`, { contents });
    } catch (e) {
      throw Error(e.message);
    }
  },

  async getCommentList({ userId, postId }) {
    try {
      return await socialApiClient.get(`/users/${userId}/posts/${postId}/comments`);
    } catch (e) {
      throw Error(e.message);
    }
  },

  async likePost({ userId, postId }) {
    try {
      return await socialApiClient.patch(`/users/${userId}/posts/${postId}/like`);
    } catch (e) {
      throw Error(e.message);
    }
  },

  async getAllPosts({ userId, offset, limit } = { limit: 5 }) {
    try {
      return await socialApiClient.get(`/users/${userId}/posts`, {
        params: {
          offset,
          limit,
        },
      });
    } catch (e) {
      throw Error(e.message);
    }
  },
};
