import apis from '@/apis';
import { call, select, put } from 'redux-saga/effects';
import { actions, selectors } from '@/data';

export function* getComments(action) {
  try {
    const postId = action.postId;
    const user = yield select(selectors.users.getUser);
    const comments = yield call(apis.postsApi.getCommentList, { userId: user.id, postId });

    yield put(actions.comments.setComments(postId, comments));
    yield put(actions.comments.getCommentsSuccess(postId, comments));
  } catch (error) {
    yield put(actions.comments.getCommentsFailure(error));
    alert(error.message);
  }
}

export function* createComment(action) {
  try {
    yield put(actions.comments.createCommentLoading());
    const { contents, postId, resetForm } = action;
    const user = yield select(selectors.users.getUser);
    yield call(apis.postsApi.createComment, { userId: user.id, postId, contents });
    yield put(actions.comments.getComments(postId));
    resetForm();
    yield put(actions.comments.createCommentSuccess());
  } catch (error) {
    yield put(actions.comments.createCommentFailure(error));
    alert(error.message);
  }
}
